body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.Main {
  grid-area: a;
  color: #02005c;
  position: relative;
}

.Main .top {
  display: flex;
  align-items: center;
  padding-left: 60px;
}

.Main .top .weather-icon {
  width: 240px;
  height: 240px;
}

.Main .top .weather-icon img {
  width: 100%;
  height: 100%;
}

.Main .top .weather-condition {
  text-align: center;
}

.Main .top .weather-condition .it-is-text {
  font-size: 22px;
  margin-bottom: -30px;
  margin-top: 150px;
}

.Main .top .weather-condition .weather-type {
  font-size: 120px;
  font-family: "Yusei Magic", sans-serif;
}

.Main .bottom {
  margin: auto;
  margin-top: 40px;
  max-width: 600px;
  padding-left: 60px;
}

.Main .bottom .location {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  font-size: 22px;
  padding: 0 10px 10px;
  border-bottom: 1px solid #02005c;
  font-weight: bold;
}

.Main .bottom .location span {
  font-size: 18px;
}

.Main .bottom .weather-data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Main .bottom .weather-data .single-data {
  width: 80%;
  display: flex;
  margin-bottom: 4px;
}

.Main .bottom .weather-data .single-data .spacer {
  flex: 1 1;
  border-bottom: 1px dashed #02005c;
  margin: 0 10px 2px;
}

.Main .bottom .weather-data .single-data .value {
  width: 20%;
}

.Sunny_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #fffeb3 100%);
}

.Cloudy_bg,
.Foggy_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #b6c4cf 100%);
}

.Rainy_bg,
.Snowy_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #60c3e5 100%);
}

.Stormy_bg {
  background: radial-gradient(circle farthest-corner at top left, #ffffff 0%, #ffbf5f 100%);
}
