.Searchbox {
  position: absolute;
  height: 36px;
  top: 20px;
  right: 20px;
  border-radius: 18px;
  background-color: #ececec;
  opacity: 0.5;
  transition: all 0.2s ease;
}

.Searchbox.show, .Searchbox:hover {
  opacity: 1;
  box-shadow: 0px 0px 10px 0px rgba(2, 0, 92, 0.2);
}

.Searchbox.status-open {
  height: 69px;
}

.Searchbox .searchbar {
  width: 260px;
  height: 36px;
  border-radius: 18px;
  padding: 0 10px 0 25px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Searchbox .searchbar input {
  height: 22px;
  border: none;
  background-color: transparent;
  flex: 1 1;
}

.Searchbox .searchbar input:focus {
  outline: none;
}

.Searchbox .searchbar .search-icon {
  width: 18px;
  height: 18px;
  margin-left: 10px;
}

.Searchbox .searchbar .search-icon img {
  width: 100%;
  height: 100%;
}

.Searchbox .status-text {
  display: none;
  opacity: 0;
  margin: 10px 0 10px 25px;
  font-size: 13px;
}

.Searchbox .status-text.show {
  display: block;
  animation: status-appear 0.1s 0.2s linear forwards;
}

@keyframes status-appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
