* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.Weekcard {
  flex: 1 1;
  display: flex;
  padding: 0 12px;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s ease;
}

.Weekcard:nth-child(even) {
  background-color: rgba(71, 71, 71, 0.1);
}

.Weekcard:hover {
  background-color: rgba(71, 71, 71, 0.2);
}

.Weekcard .date {
  text-align: center;
}

.Weekcard .date .month {
  font-size: 13px;
  font-weight: bold;
}

.Weekcard .date .day-num {
  font-size: 23px;
  font-weight: bold;
  border-bottom: 1px solid #bbb;
  padding-bottom: 2px;
}

.Weekcard .date .day-name {
  padding-top: 2px;
  font-size: 12px;
}

.Weekcard .weather-icon {
  width: 34px;
  height: 34px;
}

.Weekcard .weather-icon img {
  width: 100%;
  height: 100%;
}

.Weekcard .temperatures {
  display: -ms-grid;
  display: grid;
  grid-template: "a b" -webkit-max-content "c d" -webkit-max-content / -webkit-max-content -webkit-max-content;
  grid-template: "a b" max-content "c d" max-content / max-content max-content;
}

.Weekcard .day,
.Weekcard .day-temp {
  -ms-grid-row-align: end;
  align-self: end;
  border-bottom: 1px solid #bbb;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

.Weekcard .day,
.Weekcard .night {
  padding: 4px 8px;
  border-right: 1px solid #bbb;
  font-size: 10px;
}

.Weekcard .night,
.Weekcard .night-temp {
  -ms-grid-row-align: start;
  align-self: start;
}

.Weekcard .day-temp,
.Weekcard .night-temp {
  padding: 4px 8px;
  font-size: 13px;
  font-weight: bold;
}

.Weekcard .date,
.Weekcard .weather-icon,
.Weekcard .temperatures {
  pointer-events: none;
}
